.editor_wrapper{
    display: block;
    position: relative;
    height: 400px;
    max-height: 400px;
    overflow: auto;
    width: 700px;
    top: 200px;
    margin: 0 auto;
    background-color: #272822;
    font-family: Consolas;
    display: inline-flex;
}

.editor_line_marker_wrapper{
    display: block;
    position: relative;
    height: 100%;
    width: 50px;
    background-color: #2F3129;
    font-family: inherit;
}
.editor_line_marker{
    width: 100%;
    margin: 0;
    display: block;
    color: #808080;
    font-family: inherit;
    position: relative;
}
.editor_line_marker span{
    position: absolute;
    right: 10px;
}
.editor_line_marker i{
    position: relative;
    left: 18px;
    bottom: 2px;
    font-size: 10px;
}

.editor_code_wrapper{
    display: block;
    position: relative;
    height: 100%;
    width: 650px;
    background-color: #272822;
    font-family: inherit;
    overflow: auto;
    font-size: 0px;
}

.editor_code_line{
    display: block;
    position: relative;
    width: 100%;
    font-family: inherit;
    font-size: 16px;
    display: inline-flex;
}

.editor_code_span{
    display: block;
    position: relative;
    width: auto;
    height: 100%;
    line-height: 100%;
    outline: none;
    overflow: hidden;
    border: 0;
    padding: 0;
    margin: 0;
    background-color: transparent;
    font-family: inherit;
    font-size: inherit;
    color: white;
}

.edit_textarea{
    outline: none;
    background-color: transparent;
    position: absolute;
    width: 8px;
    border: 0;
    resize: none;
    padding: 0;
    color: white;
    font-family: inherit;
    font-size: 16px;
    overflow: hidden;
}

#text_width_finder{
    visibility: hidden;
    font-size: 16px;
    height: 20px;
    padding: 0;
}